@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.about__center {
    text-align: center;
    padding-bottom: 1.3rem;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    margin-left: 1.5rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);

    transition: var(--transition);

}

.about__me-image:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    margin-right: 1.2rem;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;

}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;

}



header {
    height: 110vh;
    padding-top: 4rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100vh;
    position: relative;
}

/*====================================CTA==========================*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/*====================================HeaderSocials==========================*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 10rem;
    bottom: 3rem;

}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*====================================ME==========================*/

.me {
    background: var(--color-primary);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding-top: 5rem 1.5rem 1.5rem 1.5rem;
}

/*====================================Scroll Down==========================*/

.scroll__down {
    position: absolute;
    right: 2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}



/*==================Media Quieries (Medium devices)=====================*/

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}




/*==================Media Quieries (small devices)=====================*/

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials{
        display: none;
       
    }
    .scroll__down {
        
        display: none;
        
    }
}
.nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);

}

.nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;

}

.nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

.nav{
    background: var(--color-bg);
    color: var(--color-white);
}



* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.4);

    --transition: all 400ms ease;

    --container-width-lg: 75 %;
    --container-width-md: 86 %;
    --container-width-ms: 90 %;

}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {

    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}

/*========================general style=======================*/

.container {
    width: 75 %;
    width: var(--container-width-lg);
    margin: 0 auto;

}


h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}


section {
    margin-top: 8rem;
    height: 130vh;
}


section h2 >,
section h5 > {
    text-align: center;
    color: rgba(255, 255, 255, 0.4);
    color: var(--color-light);

}

section h2 > {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: rgba(255, 255, 255, 0.4);
    color: var(--color-light);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition)
}

a:hover{
    color: #fff;
    color: var(--color-white);

}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn-hover{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
   background: #4db5ff;
   background: var(--color-primary);
   color: #1f1f38;
   color: var(--color-bg);

}


img{
    display: block;
    width: 100%;
    object-fit: cover;
}


/*==================Media Quieries (Medium devices)=====================*/

@media screen  and (max-width: 1024px){
    .container{
        width: var(--container-width-mg);
    }
    section{
        margin-top: 6rem;

    }
}



/*==================Media Quieries (Small devices)=====================*/

@media screen  and (max-width: 600px){
    .container{
        width: var(--container-width-sm );
    }
    section >h2{
        margin-bottom: 2rem;
        
    }
}
























